import TextInput from "../components/common/TextInput";
import Button from "../components/common/Button";
import { useForm } from "react-hook-form";
import ListBox from "../components/common/ListBox";
import { emailSendApi, mentorRegistApi } from "../api/authApi";
import { useNavigate } from "react-router-dom";

const categorys = ["", "", "", "", ""];
const grades = ["1", "2", "3", "4"];

export default function MentorRegisterPage() {
  const navigate = useNavigate();
  const { register, control, handleSubmit, formState, getValues } = useForm({
    mode: "onTouched",
    defaultValues: {
      grade: "4",
      major: "",
    },
  });

  const onSubmit = async (data) => {
    const result = await mentorRegistApi(data);
    if (result) {
      alert("  .");
      navigate("/");
      localStorage.setItem("isMentor", true);
    } else alert("  .");
  };
  const onEmailSend = async () => {
    if (formState.errors.email) {
      alert("  ");
      return;
    }
    const result = await emailSendApi({ email: getValues("email") });
    if (result) alert("  .");
    else alert("  .");
  };

  return (
    <main className="w-full h-full">
      <div className="w-full max-w-md m-auto pt-20">
        <h2 className="text-center text-3xl font-bold"> </h2>
        <h4 className="text-center text-lg pt-4 font-light">   .</h4>
        <form className="space-y-5 py-10 text-center" onSubmit={handleSubmit(onSubmit)}>
          <div className="flex items-center justify-center space-x-2">
            <TextInput
              register={register("email", {
                required: "  .",
                pattern: {
                  value: /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i,
                  message: "  .",
                },
              })}
              placeholder="  ."
            ></TextInput>
            <div className="w-20">
              <Button type="button" onClick={onEmailSend}>
                
              </Button>
            </div>
          </div>
          <TextInput register={register("code")} placeholder="   ."></TextInput>
          <ListBox list={grades} id="grade" name="grade" control={control} placeholder=" ."></ListBox>
          <ListBox
            list={categorys}
            id="major"
            name="major"
            control={control}
            placeholder=" ."
          ></ListBox>
          <Button type="submit"></Button>
        </form>
      </div>
    </main>
  );
}
