import TextInput from "../components/common/TextInput";
import Button from "../components/common/Button";
import FileInput from "../components/common/FileInput";
import { useForm } from "react-hook-form";
import { registApi } from "../api/authApi";
import { useNavigate } from "react-router-dom";

export default function RegisterPage() {
  const navigate = useNavigate();
  const { register, control, handleSubmit } = useForm({
    mode: "onTouched",
  });

  const onSubmit = async (data) => {
    const frm = new FormData();
    Object.entries(data).map((line) => frm.append(line[0], line[1]));
    console.log([...frm.entries()]);
    const result = await registApi(frm);
    if (result) {
      alert(" .");
      navigate("/");
    } else alert("!");
  };

  return (
    <main className="w-full h-full">
      <div className="w-full max-w-md m-auto pt-20">
        <h2 className="text-center text-3xl font-bold"></h2>
        <h4 className="text-center text-lg pt-4 font-light">  AjouMtoM</h4>
        <form className="space-y-5 py-10 text-center" onSubmit={handleSubmit(onSubmit)}>
          <TextInput register={register("userID", { required: true })} placeholder=" ."></TextInput>
          <TextInput register={register("nickname", { required: true })} placeholder=" ."></TextInput>
          <TextInput
            type="password"
            register={register("password", { required: true })}
            placeholder=" ."
          ></TextInput>
          <div>
            <div className="text-left text-stone-700 pb-2 text-lg"> </div>
            <FileInput id="" control={control} name="ProfileImage" type="avatar"></FileInput>
          </div>
          <Button type="submit"></Button>
        </form>
      </div>
    </main>
  );
}
